import axios from 'axios';
import { useNavigate } from 'react-router-dom';

export const useLogout = () => {
  const navigate = useNavigate();

  const logout = async () => {
    try {
      await axios.post('/log-out');
      console.log(' ');
      navigate('/login');
    } catch (error) {
      console.error(' :', error);
    }
  };

  return logout;
};
